@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
body::-webkit-scrollbar {
	display: none;
}
body {
	-ms-overflow-style: none;
	scrollbar-width: none;
}

.App{height:100vh}body{background-color:#0d1117;cursor:default}*{transition:all 1s ease;-webkit-user-select:none;-ms-user-select:none;user-select:none;box-sizing:border-box}.fade-enter{opacity:0;z-index:1}.fade-enter.fade-enter-active{opacity:1;transition:opacity 250ms ease-in}.fade-enter{opacity:0.01}.fade-enter.fade-enter-active{opacity:1;transition:opacity 500ms ease-in}.fade-exit{opacity:1}.fade-exit.fade-exit-active{opacity:0.01;transition:opacity 300ms ease-in}

.nav-elements{font-size:1.1rem;margin:auto;padding:0 10px;color:#fff;text-decoration:none}.nav-elements:hover{color:#1f6feb;font-weight:bolder}.active{color:#1f6feb;pointer-events:none;cursor:default;font-weight:bolder}

.Nav{background-color:rgba(13,17,23,0.2);justify-content:center;padding:10px 10%;padding-top:24px;font-family:'Lato', sans-serif}@media (min-width: 1030px){.Nav .container{width:70%}}

#intro{background-color:#0d1117;display:flex;height:100vh;flex-basis:auto}@media (max-width: 830px) and (min-width: 320px){#intro{margin-top:70px;flex-wrap:wrap-reverse;height:auto}}

#introText{flex-grow:1;display:flex;width:100%;flex-direction:column;margin:auto 0;margin-left:10%;font-family:PT Sans;font-size:4rem;color:#fff}#introText .typewriter{display:flex}#introText .typewriter .designation{margin-left:20px;min-width:300px;font-family:Roboto Slab;color:#1f6feb}@media (max-width: 520px){#introText{font-size:3rem}}.name{padding:0 10px;background-color:#fff;border-radius:20px;font-family:Merriweather;color:#0d1117;transition:all 0.2s ease-in}.name:hover{color:#fff;background-color:#1f6feb}

#photo{flex-grow:1;display:flex;margin:0 0;padding:0 0}#photo img{margin:auto}@media (min-width: 1000px){#photo img{height:100vh}}@media (max-width: 830px){#photo img{border-radius:500px}}

#about{background-color:#0d1117;height:100vh;width:100vw;display:flex;padding-top:30px}@media (max-width: 800px){#about{flex-wrap:wrap;padding-top:100px}#about .content-wrapper #content{margin:auto 20px !important}}

.content-wrapper{flex-shrink:1.8;display:flex;justify-content:center;align-items:center;flex-grow:1}.content{position:relative;color:#fff;text-align:justify;font-weight:bold;width:85%;list-style:none}@media (min-width: 430px){.content{left:-40px}}.content li:before{font-family:'FontAwesome';content:'\f30b';margin:0 10px 0 -15px;color:#34495e}.content .btn{margin-top:20px;background-color:#0d1117;border-color:#fff;color:#fff}.content .btn:hover{background-color:#34495e}

.timeline-container{height:100%;max-width:900px;width:100%}@media (max-width: 800px){.timeline-container{height:430px}}.timeline-container .slider{min-height:22vh;position:relative;-webkit-perspective:1000px;perspective:1000px;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;margin:40vh 0}@media (max-width: 800px){.timeline-container .slider{margin:10vh 0}}.timeline-container .slider [type='radio']{display:none}.timeline-container .slider label{margin:auto;width:80%;height:100%;border-radius:1rem;position:absolute;left:0;right:0;transition:-webkit-transform 0.4s ease;transition:transform 0.4s ease;transition:transform 0.4s ease, -webkit-transform 0.4s ease;background-color:#243342;color:#fff}.timeline-container .slider label .academic-timeline{height:100%;padding:10px;display:flex;font-size:8vw;justify-content:space-evenly}.timeline-container .slider label .academic-timeline .fas{color:#34495e;box-sizing:border-box;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;flex-basis:fit-content;margin:auto 0}.timeline-container .slider label .academic-timeline .academic-details{box-sizing:border-box;text-align:center;justify-content:middle;height:-webkit-min-content;height:min-content;margin:auto 0}.timeline-container .slider label .academic-timeline .academic-details h3,.timeline-container .slider label .academic-timeline .academic-details h6,.timeline-container .slider label .academic-timeline .academic-details h2{font-size:1.5vw;margin:0 auto;height:-webkit-min-content;height:min-content}@media (max-width: 430px){.timeline-container .slider label .academic-timeline .academic-details h3,.timeline-container .slider label .academic-timeline .academic-details h6,.timeline-container .slider label .academic-timeline .academic-details h2{font-size:3.8vw}}@media (min-width: 430px) and (max-width: 770px){.timeline-container .slider label .academic-timeline .academic-details h3,.timeline-container .slider label .academic-timeline .academic-details h6,.timeline-container .slider label .academic-timeline .academic-details h2{font-size:2vw}}.timeline-container .slider label .codingStats{height:100%;width:100%;display:flex;justify-content:space-evenly}.timeline-container .slider label .codingStats .platform{display:inline-block;height:100%;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;width:30%;display:flex;flex-direction:column;justify-content:center;padding:10px}.timeline-container .slider label .codingStats .platform .fas,.timeline-container .slider label .codingStats .platform .fab{color:#34495e;font-size:2.5vw;text-align:center;box-sizing:border-box;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;flex-basis:fit-content;margin-bottom:5px}@media (max-width: 430px){.timeline-container .slider label .codingStats .platform .fas,.timeline-container .slider label .codingStats .platform .fab{font-size:5vw}}.timeline-container .slider label .codingStats .platform h6{font-size:1.7vw;text-align:center;height:-webkit-min-content;height:min-content;margin:0}@media (max-width: 430px){.timeline-container .slider label .codingStats .platform h6{font-size:3vw}}.timeline-container .slider label .codingStats .platform .stats{text-align:center;margin:0}@media (max-width: 430px){.timeline-container .slider label .codingStats .platform .stats{font-size:2.5vw}}.timeline-container .slider label .codingStats .platform .stats i{font-size:1vw}@media (max-width: 800px){.timeline-container .slider label .codingStats .platform .stats i{font-size:2.5vw}}.timeline-container .slider .upper{box-shadow:1px 0 4px 0 rgba(0,0,0,0.37);-webkit-transform:translate3d(0, -70%, -170px);transform:translate3d(0, -70%, -170px);transition:all 0.4s ease-in-out}.timeline-container .slider .middle{box-shadow:13px 0 25px 0 rgba(0,0,0,0.3),0 11px 7px 0 rgba(0,0,0,0.19);-webkit-transform:translate3d(0, 0, 30px);transform:translate3d(0, 0, 30px);transition:all 0.4s ease-in-out}.timeline-container .slider .lower{box-shadow:1px 0 4px 0 rgba(0,0,0,0.37);-webkit-transform:translate3d(0, 70%, -170px);transform:translate3d(0, 70%, -170px);transition:all 0.4s ease-in-out}

#workPage{display:flex;flex-direction:column;min-height:100vh;padding-top:61px}

.projects{color:#fff}.projects .heading{text-align:center;margin-bottom:20px}.projects .list{display:flex;justify-content:space-evenly;flex-wrap:wrap}.projects .list .project-icon .project-image{display:flex;justify-content:center;overflow:hidden;width:250px;height:170px;margin:10px 40px;box-shadow:0 6px 6px #243342;border-radius:2rem}.projects .list .project-icon .project-image:hover{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-filter:brightness(40%);transition:all 1s ease}.projects .list .project-icon .project-image img{position:relative;margin-bottom:20px;height:100%;flex-shrink:0}.projects .list .project-icon .click-me{position:relative;text-align:center;top:-105px;display:none}.projects .list .project-icon .project-details{display:flex;justify-content:center;align-items:baseline}.projects .list .project-icon .project-details>*{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:10px 2px}.projects .list .project-icon .project-details .fa-star{color:#7d3ea0}.projects .list .project-icon .project-details .fa-github{color:#34495e;font-size:1.2rem}.projects .list .project-icon .project-details .fa-link{color:#2479d0}

#contact{height:100vh;padding-top:100px;display:flex;flex-wrap:wrap-reverse}#contact .contact-form{flex-grow:1.3;display:flex}#contact .contact-form .inputs{display:flex;flex-direction:column;height:100%;justify-content:center;margin:0 auto;width:70%}#contact .contact-form .inputs span{display:flex;flex-wrap:wrap}#contact .contact-form .inputs span .box{flex-grow:1}#contact .contact-form .inputs .box{position:relative;display:inline-block;margin:0 10px}#contact .contact-form .inputs .box input{width:100%;padding:10px 0;font-size:18px;color:#fff;margin-bottom:30px;border:none;border-bottom:1px solid #1f6feb;outline:none;background:transparent}#contact .contact-form .inputs .box .label{position:absolute;top:0;left:0;padding:10px 0;font-size:16px;color:#1f6feb;pointer-events:none;transition:0.5s}#contact .contact-form .inputs .box .active-label{top:-25px !important;left:0 !important;font-size:12px !important}#contact .contact-form .inputs textarea{padding:10px 0;font-size:18px;color:#fff;margin:0 auto;margin-bottom:30px;border:none;border-bottom:1px solid #1f6feb;outline:none;background:transparent;width:97%;height:100px;overflow-y:hidden;max-height:300px}#contact .contact-form .inputs button{border-radius:15px;background-color:#1f6feb;color:#fff;padding:10px 20px;width:-webkit-max-content;width:max-content;margin-right:20px}#contact .contact-form .inputs button:hover{background-color:#fff;color:#1f6feb}#contact .contact-form .inputs .social-media{margin-top:20px;padding:0 10px;display:flex;flex-wrap:reverse;justify-content:center}#contact .contact-form .inputs .social-media span{justify-content:center}#contact .contact-form .inputs .social-media span a{background-color:#fff;font-size:40px;height:60px;width:60px;text-align:center;border-radius:20px;margin:10px 20px 0 0}@media (max-width: 768px){#contact .contact-form .inputs{width:90%}}#contact .contact-image-holder{flex-grow:1;display:flex;height:100%;justify-content:center;align-items:center}#contact .contact-image-holder span img{font-size:100%;width:100%}@media (min-width: 768px){#contact .contact-image-holder span img{width:500px}}@media (max-width: 768px){#contact .contact-image-holder{height:-webkit-max-content;height:max-content}}@media (max-width: 768px){#contact{padding-top:900px}}

